import classes from "../home.module.scss";
import video from "assets/video/Arch_00.mp4";

const HomeHeader = () => {
  const handleNavigate = () => {
    window.open(
      "https://docs.google.com/forms/d/e/1FAIpQLScHiVaf7WHQVXEk-arCjFqxGP4LJOAqKU-PqCRpZZUBCoSVbA/viewform",
      "_blank"
    );
  };
  return (
    <div className={classes.homeHeader}>
      <video
        preload="auto"
        className={classes.video}
        autoPlay
        loop
        muted
        playsInline
        webkit-playsInline
      >
        <source src={video} type="video/mp4" />
        Your browser does not support the video tag.
      </video>
      <div className={`${classes.homeHeaderContent} rowGap-30 container`}>
        <h4 className="title-lg">
          Transforming Ideas into Powerful Digital Solutions
        </h4>
        <button onClick={() => handleNavigate()} class="btn btn-light">
          Begin Project
        </button>
      </div>
    </div>
  );
};

export default HomeHeader;
