import classes from "../home.module.scss";
import { observer } from "mobx-react-lite";
import team_icon from "assets/img/team_icon.svg";
import solutions_icon from "assets/img/solutions_icon.svg";
import support_icon from "assets/img/support_icon.svg";
import method_icon from "assets/img/method_icon.svg";
import quality_icon from "assets/img/quality_icon.svg";
import person_done from "assets/img/person-done.svg";

const HomeContent = ({ mainData }) => {
  const icons = {
    expert_team: team_icon,
    solutions: solutions_icon,
    support: support_icon,
    methodologies: method_icon,
    quality: quality_icon,
  };
  return (
    <div className="container rowGap-50">
      <div className="roundedBlock">
        <p className="paragraph-dark text-center">
          Arch Software is a forward-thinking technology solutions provider
          dedicated to crafting high-quality applications, immersive games, and
          powerful automation tools. Our team of experienced developers,
          designers, and engineers is committed to leveraging cutting-edge
          technologies and best practices to deliver solutions that propel
          businesses and individuals toward success.{" "}
        </p>
      </div>
      <div className="rowGap-100">
        <div className="rowGap-50">
          <div className="headerContent text-center">
            <h4 className="title">{mainData?.whyChoose?.title}</h4>
            <p className="paragraph-black size-20">
              {mainData?.whyChoose?.description}
            </p>
          </div>
          <div className={`${classes.choiceData} flexData`}>
            {mainData?.whyChoose?.list.map((data, i) => (
              <div className="rowGap-10" key={i}>
                <img src={icons[data.value]} alt="" />
                <h5 className="size-28">{data?.title}</h5>
                <p className="paragraph-black size-20">{data?.description}</p>
              </div>
            ))}
          </div>
        </div>
        <div className="rowGap-20">
          <h4 className="title text-center">Our Approach</h4>
          <div className={`roundedBlock ${classes.approachData}`}>
            {mainData?.order_list?.map((approach, i) => (
              <div key={i} className="rowGap-10">
                <h5 className="paragraph-black size-28 fitContent">{approach?.title}</h5>
                <p className="paragraph-black size-20">
                  {approach?.description}
                </p>
              </div>
            ))}
          </div>
        </div>
        <div className="rowGap-20">
          <h4 className="title text-center">Ready to Elevate Your Business?</h4>
          <div className={`rowGap-20 ${classes.elevateBusinessBlock}`}>
            <span>
              <img src={person_done} alt="person_done" />{" "}
              <p className="paragraph-black size-20">
                Partner with Arch Software to turn your concepts into reality.
              </p>
            </span>

            <span>
              <img src={person_done} alt="person_done" />
              <p className="paragraph-black size-20">
                Reach out today to learn how we can collaborate on your next
                project.
              </p>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default observer(HomeContent);
